import React from 'react';
import { dayLabels } from '../constants/defaultData';

const Header = ({ 
  currentView, 
  selectedDay, 
  bulkEditMode,
  onViewSwitch,
  onBulkEditToggle,
  onSettingsOpen,
  onExportData,
  onImportData,
  onClearAllData,
  connectionState,
  onlineUsers,
  websocketEnabled
}) => {
  const getDayLabel = (dayIndex) => {
    return dayLabels[dayIndex] || '';
  };

  return (
    <div className="header">
      <div className="header-left">
        <h1> </h1>
        {websocketEnabled && (
          <div className={`connection-status ${connectionState}`}>
            {connectionState === 'connected' && ' '}
            {connectionState === 'connecting' && ' ...'}
            {connectionState === 'disconnected' && ' '}
            {connectionState === 'failed' && ' '}
            {onlineUsers.size > 1 && ` (${onlineUsers.size})`}
          </div>
        )}
      </div>
      
      <div className="header-controls">
        <div className="view-toggle">
          <button 
            className={`view-btn ${currentView === 'grid' ? 'active' : ''}`}
            onClick={() => onViewSwitch('grid')}
          >
            
          </button>
          {selectedDay !== null && (
            <button 
              className={`view-btn ${currentView === 'gantt' ? 'active' : ''}`}
              onClick={() => onViewSwitch('gantt')}
            >
               ({getDayLabel(selectedDay)})
            </button>
          )}
        </div>
        
        <div className="data-controls">
          <button 
            className={`bulk-edit-btn ${bulkEditMode ? 'active' : ''}`}
            onClick={onBulkEditToggle}
          >
            {bulkEditMode ? '   ' : ' '}
          </button>
          
          <div className="dropdown">
            <button className="dropdown-btn"></button>
            <div className="dropdown-content">
              <button onClick={onSettingsOpen}> </button>
              <button onClick={onExportData}>  </button>
              <label className="import-btn">
                  
                <input 
                  type="file" 
                  accept=".json" 
                  onChange={onImportData}
                  style={{ display: 'none' }}
                />
              </label>
              <button onClick={onClearAllData} className="danger-btn">  </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header;